import React from "react";
import Card from "./card";
import './styles.scss';

const Body = React.memo((props) => {
    const { news, sector, categories } = props
    const getCategory = (noticia) => {
        return categories[(Number(noticia.type) -1 )];
    }
    return(
        <div className="BodyComponent">
                <div className="header-section">
                    <p><b>{sector.name}</b><span></span></p>
                </div>
                <div className="body-content">
                {
                    news.length > 0 && categories.length > 0 ? 
                    news.map( noticia => {
                        const cat = getCategory(noticia);
                        return <Card 
                                    key={noticia.id} 
                                    noticia={noticia} 
                                    category={cat}
                                    sector={sector.name}
                                />
                    })
                    :
                    null
                }
                </div>
        </div>
    )
})

export default Body