import React, { useEffect, useState } from 'react';
import Header from "./header/header";
import Footer from "./footer";
import { default as api } from "../../http/api";
import Wrapper from "../../components/shared/magazine-wraper";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCopyright } from "@fortawesome/free-solid-svg-icons";
import './style.scss';
import Body from './body';

const Generic = (props) => {
  const [currentNews, setCurrentNews] = useState([])
  const [noticias, setNoticias] = useState([])
  const [newsletter, setNewsletter] = useState({})
  const [sectores, setSectores] = useState([])
  const [categories, setCategories] = useState([])
  const [currentSector, setCurrentSector] = useState({})

  useEffect(() => { 
    const url = new URL(window.location.href)
    //const app = url.searchParams.get("app");
    const id = url.searchParams.get("id");
    
    api.get(`/${id}`).then(resp => {
      setNoticias(resp.data.magazine.news)
      setNewsletter(resp.data.magazine.newsletter)
      setSectores(resp.data.magazine.sectores)
      setCurrentSector(resp.data.magazine.sectores[0])
      setCategories(resp.data.magazine.categories)
      filterNews(null, resp.data.magazine.news, resp.data.magazine.sectores)
    })
    .catch(err => {
      console.log("Error", err);
      props.history.push('/notfound');
    })
  }, [])

  const filterNews = (sectorId, news, sectors = []) => {
    
    const newSector = !sectorId ? sectors[0].id: sectorId;  
    //if(newSector ===  currentSector.id) { return; }
    const noticiasDelSector = news.filter(item => item.id_sector === newSector)
    setCurrentNews(noticiasDelSector)
    const sc = sectores.length > 0 ? sectores: sectors,
          cs = sc.find(item => item.id === newSector)
    setCurrentSector(cs)
  }

  const onSelectSectorHandler = (idSector) => {
    filterNews(idSector, noticias)
  }

  return (
    <Wrapper>
      <Header 
        newsletter={newsletter} 
        handler={onSelectSectorHandler} 
        sectores={sectores}
        currentSector={currentSector}
        />
        <Footer newsletter={newsletter}/>
        <Body 
          news={currentNews} 
          categories={categories} 
          sector={currentSector}
          />
          <div className="miniFooter">
            <span><FontAwesomeIcon icon={faCopyright} /> Opemedios {new Date().getFullYear()}</span>
          </div>
    </Wrapper>
  )
}

export default Generic