import React, { useState, useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCalendar, faSun, faMoon, faAngleDoubleDown } from "@fortawesome/free-solid-svg-icons";
import "./styles.scss";

const Header = React.memo(({newsletter, handler, sectores, currentSector}) => {
    
    const [active, setActive] = useState(false);
    const [height, setHeight] = useState(45);
    
    const handleClick = (e) => {
        const activo = !active;
        const h = activo ? (sectores.length * 50) + 45: 45;
        setHeight(h);
        setActive(activo);
    }

    useEffect(() => {
        setHeight(45)
    }, [])

    const onClickSector = (idSector) => {
        handler(idSector)
    }
    
    return(
        <div className="header" id="generic">
            <div 
                className={`menu-fixed ${active?'active':''}`} 
                onClick={handleClick}
                style={{height: `${active ? height: 45}px`}}
                ><FontAwesomeIcon icon={faAngleDoubleDown} />
                    <ul>
                    { 
                        sectores.map( item => (
                            <li 
                                onClick={e => onClickSector(item.id)} 
                                key={item.id}
                                className={`${currentSector.id === item.id ? 'active': ''}`}
                            >
                                <span>{item.name}</span>
                            </li>
                        ))
                    }
                    </ul>
                </div>
            <img src={newsletter.banner} alt="img"/>
            <div className="cintillo">
                <span className="horario">
                    { 
                        newsletter.horary === "Matutino" ? <FontAwesomeIcon icon={faSun}/>: <FontAwesomeIcon icon={faMoon}/>
                    }
                    {" " + newsletter.horary}
                </span>
                <span className="fecha float-right">
                <FontAwesomeIcon icon={faCalendar}/> {newsletter.fecha}
                </span>
            </div>
        </div>
    )
})

export default Header