import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLongArrowAltRight } from "@fortawesome/free-solid-svg-icons";

const Card = React.memo((props) => {
    const { noticia, sector, category } = props
    return(
        <div className="card-item">
            <span className="card-item-category">
                {sector}
            </span>
            <div className="card-item-title">
                <a href={noticia.link} target="_blank" rel="noopener noreferrer">
                    {noticia.title}
                </a>
            </div>
            <div className="card-item-body">
                <div className="card-item-body-text" dangerouslySetInnerHTML={{ __html: noticia.text }} >
                </div>
                <div className="card-item-body-source">
                    {category} | {noticia.source}
                </div>
                <a href={noticia.link} target="_blank" rel="noopener noreferrer" className="card-item-body-button">VER <FontAwesomeIcon icon={faLongArrowAltRight} /></a>
            </div>
        </div>
    )
})

export default Card