import axios from 'axios';

//const API_URL = 'http://localhost/newsletters/demo/index.php/getmagazine/';
const API_URL = "https://opemedios.mx/newsletters/cocacola/index.php/getmagazine/"

const getClient = (baseUrl = null) => {

  const options = {
    baseURL: API_URL
  };

  options.headers = {
      'Content-type': 'application/json'
  }

  const client = axios.create(options);

  return client;
};

/**
 * Base HTTP Client
 */
export default {
  // Provide request methods with the default base_url
  get(url, conf = {}) {
    return getClient().get(url, conf)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  },

  delete(url, conf = {}) {
    return getClient().delete(url, conf)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  },

  post(url, data = {}, conf = {}) {
    return getClient().post(url, data, conf)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  },

  put(url, data = {}, conf = {}) {
    return getClient().put(url, data, conf)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  },

  patch(url, data = {}, conf = {}) {
    return getClient().patch(url, data, conf)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  },
};
